*{
  padding : 0;
  margin : 0;
  box-sizing : border-box;
  font-family: 'Poppins',sans-serif;
  text-decoration: none;
  list-style: none;
  scroll-behavior: smooth;
}
:root{
  --bg-color: #2a2a2a;
    --second-bg-color: #202020;
    --text-color: #fff;
    --second-color: #fff;
    --main-color: #61dc14;
    --big-font: 5rem;
    --h2-font: 3rem;
    --p-font: 1.1rem;
}
.body{
  background: var(--bg-color);
  color: var(--text-color);
}

header{
  position: fixed;
  width: 100%;
  top: 0;
  right: 0;
  z-index: 1000;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: transparent;
  padding: 22px 15%;
  border-bottom: 1px solid transparent;
  transition: all.45s ease;
}

.logo{
  color: var(--text-color); 
  font-size: 35px;
  font-weight: 700;
  letter-spacing: 1px;
}

span{
  color: var(--main-color);
}
.navlist{
  display: flex;
}
.navlist a{
  color: var(--main-color);
  font-size: 17px;
  font-weight: 500;
  margin: 0 25px;
  transition: all .45s ease;
}
.navlist a:hover{
  color: var(--main-color);
}
.navlist a.active{
  color: var(--main-color);
}

section{
  padding: 160px 15% 120px;

}

/* Home Section */

#home{
  position: relative;
  height: 100vh;
  width: 100%;
  background-image: url("./assests/banner.png");

  background-size: cover;
  background-position: center;
  display: flex;
  align-items: center;
  justify-content: flex-start;

}

.slide{
  margin-bottom: 20px;
}
.one{
  display: inline-block;
  margin-right: 15px;
  height: 32px;
  padding: 0 15px;
  line-height: 32px;
  font-size: 20px;
  font-weight: 500;
  border-radius: 5px;
  background: var(--main-color);
  color: var(--text-color)
}
.two{
  display: inline-block;
  color:var(--second-color);
  font-size: 20px;
  font-weight: 500;

}

.nav-link-home h1{
  font-size: var(--big-font);
  font-weight: 700;
  color: var(--text-color);
  line-height: 1.1;
  margin: 0 0 8px;

}
.nav-link-home h3{
  color: var(--text-color);
  margin: 0 0 35px;
  font-size: 3rem;
  font-weight: 700;
  

}
.nav-link-home p{
  color: var(--second-color);
  font-size: var(--p-font);
  line-height: 1.1;
  margin-bottom: 40px;
}

.button{
  display: inline-flex;
  align-items: center;
  justify-content: center;
}
#resume-link-2{
  display: inline-block;
  padding: 12px 25px 12px 10px;
  background: var(--main-color);
  border-radius: 5px;
  color: var(--text-color);
  font-size: 1rem;
  letter-spacing: 1px;
  font-weight: 600;
  transition: all .45s ease;
  border:  solid 0px;
}
#resume-link-2:hover{
  transform: scale(2.0);
}

#resume-link-1{
  border: 0px solid white;
}
#user-detail-skill{
  font-size: 45px;
  font-weight: 500;
}


#user-detail-name{
  font-size: 55px;
  font-weight: 800;
}

header.sticky{
  background: var(--bg-color);
  border-bottom:  1px solid #ffffff1a;
  padding:  12px 15%;
}


/* About Section */

#about{
  border: 0px solid crimson;
  display: grid;
  grid-template-columns: repeat(2,1fr);
  align-items: center;  

}
.about-img img{
  border: 0px solid crimson;
  max-width: 350px;
  height: auto;
  width: 80%;
  border-radius: 10%;
  align-items: end;
  
}

.nav-link-about h2{
  font-size: var(--h2-font);
  line-height: 1;
}
.nav-link-about h4{
  font-size: 24px;
  font-weight: 600;
  color: var(--text-color);
  line-height: 1.7;
  margin:  15px 0 30px;
}
.nav-link-about p{
  color: var(--second-color);
  font-size: var(--p-font);
  line-height: 1.8;
  margin-bottom: 4rem;
}
